@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
    font-family: 'Helvetica';
    src: url("./customeFonts/helvetica-compressed-5871d14b6903a.otf") format('ttf');
    font-style: normal;

}

.jost-400 {
    font-family: "Jost", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
}

.outfit {
    font-family: "Outfit", sans-serif;
}

.hide {
    opacity: 0;
    transition: opacity 0ms ease-in-out;
}

.show {
    opacity: 1;
    transition: opecity 900ms ease-in-out 900ms forwards;
    
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}